import React from "react";
import { useEffect, useState } from "react";
import { reset } from "yargs";
import s from "./ModalProduct.module.css";

const INITIAL_STATE = {
  name: "",
  count: "",
  weight: "",
  width: "",
  height: "",
  comments: "",
  isFullTime: false,
};

const ModalProduct = () => {
  const [formData, setFormData] = useState({ ...INITIAL_STATE });
  const [newProduct, setNewProduct] = useState("");

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    const isCheckBox = type === "checkbox";
    setFormData({
      ...formData,
      [name]: isCheckBox ? checked : value,
    });
  };

  useEffect(() => {
    const onEscPress = (e) => {
      if (e.code === "Escape") {
        onclose();
      }
    };

    window.addEventListener("keydown", onEscPress);
    return () => {
      window.removeEventListener("keydown", onEscPress);
    };
  }, []);

  const handleSubmit = (e) => {
    e.preventDefault();
    setNewProduct({
      ...formData,
    });
    reset();
  };

  const reset = () => {
    setFormData({ ...INITIAL_STATE });
  };

  return (
    <div className={s.backdrop}>
      <div
        className={s.modal}
        //   onClick={handleBackdropClick}
      >
        <div>
          <header className={s.header}>
            <button
              type="button"
              className={s.closeBtn}
              //   onClick={onClose}
              aria-label="Close"
            >
              &times;
            </button>
          </header>

          <div>
            <h2 className={s.title}>Main information about product</h2>
            <form action="" className={s.form} onSubmit={handleSubmit}>
              <label htmlFor="" className={s.label}>
                name
                <input
                  type="text"
                  name="name"
                  value={formData.name}
                  placeholder="input name product"
                  required
                  onChange={handleChange}
                  className={s.input}
                />
              </label>
              <label htmlFor="" className={s.label}>
                count
                <input
                  type="number"
                  name="count"
                  value={formData.count}
                  placeholder="input count"
                  required
                  onChange={handleChange}
                  className={s.input}
                />
              </label>
              <label htmlFor="" className={s.label}>
                weight
                <input
                  type="text"
                  name="weight"
                  value={formData.weight}
                  placeholder="input weight"
                  required
                  onChange={handleChange}
                  className={s.input}
                />
              </label>
              <div>
                <label htmlFor="" className={s.label}>
                  width
                  <input
                    type="text"
                    name="width"
                    value={formData.width}
                    placeholder="input width"
                    required
                    onChange={handleChange}
                    className={s.input}
                  />
                </label>
                <label htmlFor="" className={s.label}>
                  height
                  <input
                    type="text"
                    name="height"
                    value={formData.height}
                    placeholder="input height"
                    required
                    onChange={handleChange}
                    className={s.input}
                  />
                </label>
              </div>
              <label htmlFor="" className={s.label}>
                comments
                <input
                  type="text"
                  name="comments"
                  value={formData.comments}
                  placeholder="input comments"
                  onChange={handleChange}
                  className={s.input}
                />
              </label>
              <div className={s.check}>
                <p> I'am stuff of this company</p>
                <input
                  name="isFullTime"
                  type="checkbox"
                  checked={formData.isFullTime}
                  onChange={handleChange}
                  required
                />
              </div>
              <div className={s.btnModal}>
                <button type="submit" text="Confirm" className={s.button}>
                  Confirm
                </button>
                <button type="submit" text="Cancel" className={s.button}>
                  Cancel
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ModalProduct;
